import { css } from '@emotion/react';

import { Typography } from 'src/components/dataDisplay';
import { FullWidthBox, Stack } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import type { Survey } from 'src/hooks/useSurveysForBestFit';

const paperLayout = css`
  display: flex;
  align-items: center;
  justify-content: center;

  text-align: center;
  color: #ffffff;

  padding: 0px 20px;
`;

const fullWidth = css`
  flex: 1;
`;
const optionLayout = css`
  height: 160px;
  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid #ffffff;
  border-radius: 8px;
  color: #ffffff;
  background: transparent;

  width: 100%;

  overflow: hidden;
  word-break: keep-all;
  overflow-wrap: break-word;
`;

interface BestFitProductPaperProps extends Survey {
  imageUrl?: string;
}

const BestFitProductPaper = ({
  imageUrl,
  question,
  options,
}: BestFitProductPaperProps): JSX.Element => {
  const questions = question.split('\n');
  return (
    <FullWidthBox>
      <Paper backgroundUrl={imageUrl} css={paperLayout}>
        <Stack spacing={40} justifyContent="center" css={fullWidth}>
          <Typography variant="h3">
            {questions.map((text, index) => {
              const key = `${index}-${text}`;
              return <div key={key}>{text}</div>;
            })}
          </Typography>
          <Stack direction="row" spacing={20} justifyContent="space-between">
            {options.map((option, index) => {
              const key = `${index}-${option}`;
              const texts = option.split('\n');
              return (
                <button key={key} css={optionLayout} type="button">
                  {texts.map((text, lineIndex) => {
                    const lineKey = `${lineIndex}-${text}`;
                    return (
                      <span key={lineKey}>
                        {text}
                        <br />
                      </span>
                    );
                  })}
                </button>
              );
            })}
          </Stack>
        </Stack>
      </Paper>
    </FullWidthBox>
  );
};

export default BestFitProductPaper;
