import { css } from '@emotion/react';

import type { LinkableContent } from 'src/hooks/useMagazineItems';

const noButton = css`
  display: contents;
  color: inherit;
`;

export interface LinkableProps extends Pick<LinkableContent, 'link'> {
  children: React.ReactNode;
}
const Linkable = ({
  children,
  link,
  ...others
}: LinkableProps): JSX.Element => {
  const handleClick = () => {
    if (link) {
      window.location.href = link;
    }
  };

  return (
    <button type="button" onClick={handleClick} css={noButton} {...others}>
      {children}
    </button>
  );
};

export default Linkable;
